export default {
    props:['module','role'],
 data(){
     return{
         addPermModel:false,
         valid:false,
        snackbar:false,
         color:'',
         text:'',
         loading:false
     }
 },methods:{
     async submit(){
       if(this.$refs.form.validate())
       {
          this.valid = true
          this.loading=true
             try {
                 const data ={roleId:this.role.id,systemmoduleId:this.module.id}
                 await this.$axios.post('api/admin/role/unassignsystemmodule',data).then((res)=>{
                        this.loading = false
                        this.color="success"
                        this.snackbar=true
                        this.text=res.data.message
                        this.$store.dispatch('roles/getAssignedModules',this.role.id) 
                        this.$refs.form.reset()
                        this.addPermModel = false

                 })
             }catch (err) {
                 this.loading = false
                 this.color="error"
                this.snackbar=true
                this.text=err.response.data.message
            }
       }
     }
 }
}
